/* WooCommerce Globals
----------------------------------------------------- */

.woocommerce {

	&.full-width-content {

		.content {
			width: 100%;
			max-width: $desktop-max-width;
		}
	}

	.entry.product {
		padding: 0;
	}

	abbr.required,
	ins {
		text-decoration: none;
	}

	span.onsale {
		min-height: auto;
		border-radius: 0;
		background-color: $blue;
		line-height: $line-height--large;
	}

	fieldset {
		margin-bottom: 20px;
	}

	.products {

		.star-rating {
			padding: 10px;
		}
	}

	p {

		&.stars {

			a,
			a:focus,
			a:hover {
				outline: 0;
			}
		}
	}

	.woocommerce-LoopProduct-link {
		display: block;
	}
}

.woocommerce-page {

	&.full-width-content {

		.content {
			width: 100%;
			max-width: $desktop-max-width;
		}
	}
}
